#add-todo{
  display: -ms-flexbox;
  display: flex;
}

#add-todo input[type="text"]{
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 7;
        flex-grow: 7;
    padding: 10px;
    border: 2px solid #7f4cc1;
    border-radius: 4px 0 0 4px;
    color: #777;
}

#add-todo input[type="submit"]{
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    -ms-flex-positive: 3;
        flex-grow: 3;
    padding: 10px;
    background: #7f4cc1;
    border-radius: 0 4px 4px 0;
    border: 2px solid #7f4cc1;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
}



