body{
  background: #eee;
  margin: 10% 15% 0% 15%;
  width:70%;
  font-family: tahoma;
}

#root{
  width: 80%;
  margin: 0 auto;
  background: #fff;
  border-top: 3px solid #7f4cc1;
  padding: 10px;
  border-radius: 2px;
  -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.2);
          box-shadow: 1px 3px 5px rgba(0,0,0,0.2);
  color: #333;
}

#root p{
  color: #777;
  font-style: italic;
}

#todo-list ul{
  list-style-type: none;
  padding: 0;
}

.nav{
  display:-ms-flexbox;
  display:flex;

}

.nav li{
  margin-right: 15px;
}

.active{
  font-weight: bold;
}

.container ul {
    list-style-type: none;
}

a{
  text-decoration: none;
  color: #7f4cc1;
}

a:hover{
  text-decoration: underline;
}



